:root {
  --cell-margin: 3vh;
  --indicator-width: 15px;
}

.timetable-page-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  transition: left .6s cubic-bezier(0.83, 0, 0.17, 1);
}

.cell {
  border-radius: 1rem;
  margin: 0 1rem;
  margin-bottom: var(--cell-margin);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  cursor: pointer;
  transition: height .6s cubic-bezier(0.83, 0, 0.17, 1);
  flex-grow: 1;
}

.bg {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  border-radius: 1rem;
}

.bg div {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: clamp(13px, calc(13px + 12 * (100vw - 300px) / 600), 25px);
}

.cell {
  margin-top: var(--cell-margin);
}

.date {
  width: 100%;
  height: 63px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Poppins", sans-serif;
  font-size: clamp(18px, calc(18px + (22) * (100vw - 300px) /1000), 40px);
  color: black !important;
}

[class|=bottom] {
  position: absolute;
  height: 100%;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 0;
  border-radius: 1rem;
}

.bottom-0 {
  top: 1vh;
  width: 95%;
  opacity: .5;
}

.bottom-1 {
  top: 2vh;
  width: 85%;
  opacity: .3;
}

@media screen and (min-width: 500px) {

  .bg {
    display: grid;
    grid-template: repeat(3, 1fr) / 1fr 1fr;
  }

  .bg div {
    justify-content: center;
    padding-left: 1rem;
  }

  .bg div:nth-child(1) {
    justify-content: center;
    grid-row: 1 /2;
    grid-column: 1 / 3;
  }

  .bg div:nth-child(2) {
    grid-row: 2 /3;
  }

  .bg div:nth-child(3) {
    grid-row: 2 /3;
  }

  .bg div:nth-child(4) {
    grid-row: 3 /4;
    grid-column: 1 / 3;
  }
}

.test {
  display: flex;
  flex-direction: row;
}